.news {
    overflow: visible;
    &-menu {
        .border-style {
            padding: 0;
            margin-top: 2px;
            &:before {
                position: absolute;
                content: "";
                height: 2px;
                width: 140px;
                left: 10px;
                top: 0;
                background: linear-gradient(to right, var(--Flamenco) 35px, var(--Wedgewood) 35px);
            }
            li {
                padding: 19px 35px 19px 20px;
            }
        }
    }
    &-sorting {
        padding: 18px 30px;
        margin-bottom: 40px;
        border: 1px solid var(--Gainsboro);
        button {
            line-height: 1.9;
            transition: all 0.2s;
            &:not(:last-child) {
                margin-right: 27px;
            }
            &:hover {
                color: var(--Flamenco);
            }
        }
        &__active {
            color: var(--Flamenco);
        }
    }
    &__item {
        display: flex;
        padding: 30px;
        border: 1px solid var(--Gainsboro);
        margin-bottom: 20px;
        transition: all 0.2s;
        .card-img {
            height: 187px;
            width: 295px;
            margin: 0 40px 0 0;
            flex-shrink: 0;
        }
        &-info {
            flex-shrink: 1;
        }
        &-date {
            margin: 0 0 15px;
            font-weight: 600;
            color: var(--Charcoal);
        }
        &-title {
            display: inline-block;
            position: relative;
            padding-bottom: 20px;
            margin-bottom: 15px;
            font-weight: 600;
            line-height: 1.3;
            text-transform: uppercase;
            transition: all 0.2s;
            &:hover {
                color: var(--Wedgewood);
            }
            &:after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                display: block;
                height: 3px;
                width: 50px;
                background-color: var(--Gainsboro);
            }
        }
        &-txt {
            margin: 0 0 20px;
            line-height: 1.4;
        }
        .arrowDown {
            display: inline-block;
            padding-right: 17px;
            span {
                border-bottom: 1px dashed var(--Charcoal);
                color: var(--Charcoal);
                transition: all 0.2s;
            }
            &:after {
                transform: rotate(-90deg);
                right: 0;
                transition: all 0.2s;
            }
            &:hover {
                span {
                    border-color: var(--White);
                }
                &:after {
                    color: var(--Flamenco)
                }
            }
        }
        &:hover {
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);
        }
    }
    &-result {
        padding-bottom: 40px;
        .pagination {
            margin-top: 10px;
        }
    }
}
@media(max-width: 991px) {
    .news {
        &-sorting {
            padding: 8px 20px;
        }
        &__item {
            padding: 20px;
            margin-bottom: 10px;
            .card-img {
                margin-right: 20px;
                width: 210px;
                height: 135px;
            }
            &:last-of-type {
                margin-bottom: 20px;
            }
        }
    }
}
@media(max-width: 767px) {
    .news {
        &-result {
            padding-bottom: 10px;
        }
    }
}
@media(max-width: 575px) {
    .news {
        &__item {
            padding: 0 0 24px;
            border: none;
            border-bottom: 1px solid var(--Gainsboro);
            flex-direction: column;
            .card-img {
                width: 100%;
                height: 184px;
                margin-bottom: 20px;
            }
            &-title {
                padding-bottom: 15px;
                margin-bottom: 13px;
                font-size: 15px;
            }
            &-txt {
                margin-bottom: 17px;
            }
            &:hover {
                box-shadow: none;
            }
        }
        &-result {
            padding-bottom: 20px;
        }
    }
}
